import React from 'react'
import Carousel from "react-elastic-carousel";


const breakPoints = [
    { width: 1, itemsToShow: 1 },
    { width: 600, itemsToShow: 2 },
    { width: 800, itemsToShow: 3 },
  ];

const RenderProgram = ({item}) =>{
    return(
        <div className="program">
            <img  className="program-image img-responsive" src={"img" +item.img} alt=""/>
            <div>

                <h3 className="uppercase"> {item.text} </h3>
                <a className="button button-light popup-html" href="sample-program1.html">read more</a> 
            </div>
        </div>
    )
}

export default function Programs({programs}) {
    const Programs = programs.map((item)=>{
        return   <RenderProgram item={item} />
    })
    return (
        <section id="program"   >
            <div className="container">
                <div className="row">

                    <div className="col-lg-12">
                        <h2 className="uppercase">
                            programs </h2>
                        <br/>
                    </div>

                    <div className="col-lg-12 programs">
                        <Carousel focusOnSelect={true} breakPoints={breakPoints}>
                            { programs && Programs}
                        </Carousel>
                    </div>
                </div>
            </div>
        </section>
    )
}
