import React , {useState} from 'react'

import { NavLink } from "react-router-dom";
import { NavHashLink  } from 'react-router-hash-link';

export default function Header() {
    const [isNavCollapsed, setIsNavCollapsed] = useState(true);

    const handleNavCollapse = () => setIsNavCollapsed(!isNavCollapsed);

    return (
            <header className="banner">
                <nav className="navbar navbar-custom " role="navigation">
                     <div className="container">
                        <div data-scroll-header className="navbar-header">
                            <button type="button" className="navbar-toggle" onClick={handleNavCollapse}>
                                <span className="sr-only">Toggle navigation</span>
                                <span className="icon-bar"></span>
                                <span className="icon-bar"></span>
                                <span className="icon-bar"></span>
                            </button>
                            <NavLink to="/home" className="navbar-brand">
                                <img src="img/siteLogo.png" alt="logo"/>
                            </NavLink>
                        </div>

                        <div className={`${isNavCollapsed ? 'collapse' : ''} navbar-collapse`} id="nav">
                            <ul className="nav navbar-nav navbar-right nav-effect uppercase">
                                <li><NavLink  to="/home"><span data-hover="Home">Home</span></NavLink></li>
                                <li><NavHashLink  to="/home#about"  ><span data-hover="About">About</span></NavHashLink></li>
                                <li><NavHashLink  className="nav-link"  to="/home#program" ><span data-hover="programs">Programs</span></NavHashLink></li>
                                <li><NavHashLink  to="/home#stories" ><span data-hover="stories">Stories</span></NavHashLink></li>
                                <li><NavHashLink  to="/home#gallery" ><span data-hover="gallery">Gallery</span></NavHashLink></li>
                                <li><NavHashLink  to="/home#news" ><span data-hover="news">News</span></NavHashLink></li>
                                <li><NavHashLink  to="/contact" ><span data-hover="contact">Contact</span></NavHashLink></li>
                                <li ><NavLink to='/donate' > <span data-hover="donate">Donate</span></NavLink></li>
                                <li><a data-scroll href="indexAr.html"><span data-hover="عربي" > عربي </span></a></li>      
                            </ul>
                        </div>
                    </div>
                </nav>
            </header>
    )
}
