import React  from 'react'
import { Link } from "react-router-dom";
import Carousel from "react-elastic-carousel";


  const breakPoints = [
    { width: 1, itemsToShow: 1 },
    { width: 500, itemsToShow: 2 },
    { width: 700, itemsToShow: 3 },
    { width: 1000, itemsToShow: 4 },
  ];

function RenderStore({item}){
    return(
        <div className="story">
            <iframe src={item.url} width="100%" frameborder="0"
                target="blanck">
            </iframe>
            <div>
                <div className="icon">
                    <i className="fa fa-2x fa-pencil"></i>
                </div>
                <h3 className="uppercase">{item.title}</h3>
                <p> {item.text} </p>
            </div>
        </div>
    )
}

export default function Stories({stories}) {
    const Stories = stories.map((item)=>{
        return (
            <div>
                <RenderStore key={item.id} item={item}/>
            </div>
        )
            
    })
    
    return (
        <section id="stories">
            <div className="container">
                <div className="row">

                    <div className="col-lg-12">
                        <h2 className="uppercase">stories</h2>
                        <br />
                    </div>

                    <div className="col-lg-12 stories">
                        
                        <Carousel focusOnSelect={true} breakPoints={breakPoints}   >  
                            {Stories}
                        </Carousel>
                    </div>

                    <div className="col-lg-7">
                        <a href="https://www.launchgood.com/campaign/masarat_elearning_for_syrian_students#!/"
                            target="_blank">
                            <div id="donate">
                                <p className="lead uppercase">You’ve helped us raise a staggering</p>
                                <span className="pledged">$ 1,715</span>

                                <div className="progress">
                                    <div className="progress-bar" role="progressbar" aria-valuenow="60" aria-valuemin="0"
                                        aria-valuemax="100" style={{width: "10%", fontWeight:'blod'}} >

                                        <span className="sr-only">10% Complete</span>

                                    </div>
                                </div>
                            </div>
                            <h4 style={{color:"black"}}> click here </h4>
                        </a>

                    </div>

                    <div className="col-lg-5 text-right">
                        <p className="lead text-right">
                            Educate a child Since the beginning of 2020, we have successfully made
                            education accessible to 400 students who were unable to attend schools due to the current
                            circumstances in Syria.
                        </p>
                        <Link to="/donate" replace>
                            <a className="button button-big button-dark"  target="blanck">donate</a>
                        </Link>
                    </div>

                </div>
            </div>
        </section>
    )
}
