import React, { useState } from 'react'
import Carousel from "react-elastic-carousel";
import ModalImage  from "react-modal-image";
import { LightBox } from 'react-lightbox-pack'; // <--- Importing LightBox Pack
import "react-lightbox-pack/dist/index.css";

const breakPoints = [
    { width: 1, itemsToShow: 1 ,itemsToScroll:1 },
    { width: 500, itemsToShow: 3,itemsToScroll:3 },
    { width: 700, itemsToShow: 4,itemsToScroll:4 },
    { width: 1000, itemsToShow: 6,itemsToScroll:6 },
];


const RenderCard = ({item,lightBoxHandler,index})=>{
    

    return(
        <div className="wrap" onClick={() =>lightBoxHandler(true, index)} >
                
            {/* <ModalImage className="img-responsive"
                small={'img/'+item.img}
                large={'img/'+item.img}
            />  */}
            
            <img  className="img-responsive"  key={item.id}  src={item.image} alt="" />
            <div className="plus"></div>    
            
        </div>
    )
}


export default function Gallery({images}) {
    const [toggle, setToggle] =  useState(false);
	const [sIndex, setSIndex] =  useState(0);

	const  lightBoxHandler  = (state, sIndex) => {
		setToggle(state);
		setSIndex(sIndex);
	}

    const cards= images.map((item, index)=>{
        return(
            <RenderCard lightBoxHandler={lightBoxHandler} index={index} item={item}/>
        )
    })

    return (
    <div>
        <div className="container">
            <div className="row">
                <div className="col-lg-12">
                    <h2 className="uppercase" style={{color: "black",marginTop:"60px"}}>gallery</h2>
                    <hr style={{borderTop:" 2px solid #6cc6e4", width: "130px" , marginLeft: "0px" }}/>
                </div>
            </div>
        </div>
        <div id="gallery">
            <Carousel focusOnSelect={true} breakPoints={breakPoints} >
                {cards}
                
            </Carousel>
            <LightBox
                state={toggle}
                event={lightBoxHandler}
                data={images}
                setImageIndex={setSIndex}
                imageIndex={sIndex}
            />
            
        </div>
    </div>
    )
}
