import React from 'react'
import CountUp from 'react-countup';

export default function Milestone() {
    const info ={
        program:3,
        student:2337,
        volunteer:100,
        teachingHour:1800,
    }
    return (
        <div id="milestone">
            <div className="container">
                <div className="row">
                    <div className="col-lg-8 col-lg-offset-2 text-center">
                        <div className="row">
                            <div className="fact col-xs-12 col-md-3 col-lg-3">
                                <p className="timer" ><CountUp end={info.program} duration={2} /></p>
                                <p>program</p>
                                <i className="fa fa-2x fa-cubes"></i>
                            </div>

                            <div className="fact col-xs-12 col-md-3 col-lg-3">
                                <p className="timer"><CountUp end={info.student} duration={3} /></p>
                                <p>student</p>
                                <i className="fa fa-2x fa-child"></i>
                            </div>

                            <div className="fact col-xs-12 col-md-3 col-lg-3">
                                <p className="timer"><CountUp end={info.volunteer} duration={3} /></p>
                                <p>volunteer</p>
                                <i className="fa fa-2x fa-users"></i>
                            </div>
                            <div className="fact col-xs-12 col-md-3 col-lg-3">
                                <p className="timer"><CountUp end={info.teachingHour} duration={3} /></p>
                                <p>teaching hour</p>
                                <i className="fa fa-2x fa-pencil"></i>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}
