import React from 'react'

export default function Aboutus() {
    return (
        <section id="about">
            <div className="container">
                <div className="row">
                    <div className="col-lg-12">
                        <h2 className="uppercase">about us</h2>
                        <br/>
                    </div>

                    <div className="col-lg-12">
                        <img className="about-image img-responsive" src="img/about.jpg" alt=""/>

                        <div className="row">
                            <br/>
                            <p className="lead col-lg-4">This initiative was founded based on our firm believe that knowledge is
                                the key to success in life for individuals and to the prosperity of societies, and that
                                learning is a lifelong, ongoing process.</p>

                            <p className="lead col-lg-4">“Masarat” was launched as a voluntary educational non-profit initiative
                                with the aim of making knowledge accessible to students anywhere (with a focus on areas in crisis) </p>

                            <p className="lead col-lg-4">We, in Masarat, are fully aware of the importance of providing equal
                                learning opportunities and of the major influence education imposes in advancing the
                                creativity of individuals in a way that enables them to build a better future for themselves
                                and the societies they are part of.</p>
                        </div>

                        <a className="button button-big button-light" href="masaratprofileEnglish.pdf" target="blanck" style={{ textDecoration: "none"}}>Download Profile</a>

                    </div>
                </div>
            </div>
        </section>
    )
}
